// components/simple-hero-carousel.tsx
import Image from 'next/image'
import { Play, Plus } from 'lucide-react'
import Link from 'next/link'

interface SimpleHeroCarouselProps {
  animes: {
    mal_id: number
    title: string
    synopsis?: string
    images: {
      jpg: {
        large_image_url: string
        image_url: string
        small_image_url: string
      }
    }
  }[]
  height?: string
}

export function SimpleHeroCarousel({
  animes,
  height = 'h-[20rem] md:h-[30rem]',
}: SimpleHeroCarouselProps) {
  if (animes.length === 0) return null

  // Ambil hanya 5 pertama ‚Äî pastikan tidak duplikat via judul (opsional)
  const uniqueAnimes = animes
    .filter((anime, index, self) =>
      index === self.findIndex(a => a.title.trim() === anime.title.trim())
    )
    .slice(0, 5)

  if (uniqueAnimes.length === 0) return null

  return (
    <div className="relative w-full overflow-hidden rounded-xl">
      <div className={`w-full ${height} relative`}>
        {uniqueAnimes.map((anime, idx) => (
          <div
            key={anime.mal_id}
            className={`absolute inset-0 transition-opacity duration-700 ${
              idx === 0 ? 'opacity-100 z-10' : 'opacity-0 z-0'
            }`}
          >
            <Image
              src={
                anime.images?.jpg.large_image_url ||
                anime.images?.jpg.image_url ||
                anime.images?.jpg.small_image_url ||
                '/placeholder.jpg'
              }
              alt={anime.title}
              fill
              className="object-cover"
              priority={idx === 0}
            />
            <div className="absolute inset-0 bg-gradient-to-t from-black/70 via-black/20 to-transparent" />
            
            <div className="absolute bottom-8 left-4 sm:left-6 md:bottom-12 md:left-12 max-w-[90%] z-10">
              <h2 className="text-xl sm:text-2xl md:text-5xl font-bold mb-2 md:mb-3 line-clamp-1 text-white">
                {anime.title}
              </h2>
              <p className="text-xs sm:text-sm md:text-base text-gray-200 line-clamp-3 max-w-md mb-3">
                {anime.synopsis || 'No description available.'}
              </p>
              <div className="flex gap-3">
                <Link
                  href={`/watch/${anime.mal_id}`}
                  className="flex items-center gap-2 bg-primary px-4 py-2 rounded hover:bg-primary/80 transition-colors text-xs sm:text-sm"
                >
                  <Play size={16} />
                  <span>Watch</span>
                </Link>
                <button
                  className="flex items-center gap-2 bg-white/20 px-4 py-2 rounded hover:bg-white/30 transition-colors text-xs sm:text-sm text-white"
                  onClick={(e) => {
                    e.preventDefault()
                    // TODO: add to watchlist
                    alert(`Added to list: ${anime.title}`)
                  }}
                >
                  <Plus size={16} />
                  <span>Add</span>
                </button>
              </div>
            </div>
          </div>
        ))}

        {/* Pagination dots */}
        {uniqueAnimes.length > 1 && (
          <div className="absolute bottom-3 left-1/2 transform -translate-x-1/2 flex gap-2 z-20">
            {uniqueAnimes.map((_, i) => (
              <span
                key={i}
                className={`w-2 h-2 rounded-full transition-colors ${
                  i === 0 ? 'bg-white' : 'bg-white/50'
                }`}
              />
            ))}
          </div>
        )}
      </div>

      {/* Label ‚Äî tambahkan sedikit branding agar tidak terasa statis */}
      <div className="absolute top-4 right-4 bg-black/50 text-white px-2 py-1 rounded text-xs">
        Top Anime
      </div>
    </div>
  )
}